:root {
  --color-theme: #20dfe3;
  --color-black: #2c2c2c;
  --color-gold: #fee261;
  --color-grey: #e6e6e6;
  --color-white: #fff;

  --color-bg-dark: #191149;
  --color-bg-grey: var(--color-grey);
  --color-bg-theme: #d2f9f9;
  --color-bg-theme-dark: #5538f4;

  --color-btn-bg-primary: var(--color-theme);
  --color-btn-bg-primary-hover: var(--color-black);

  --color-btn-primary: var(--color-white);
  --color-btn-primary-hover: var(--color-white);

  --color-btn-bg-secondary: #5538f4;
  --color-btn-bg-secondary-hover: var(--color-black);

  --color-btn-secondary: var(--color-white);
  --color-btn-secondary-hover: var(--color-white);

  --color-form-field: #8874f7;
}

body {
  font-family: "Poppins", sans-serif;
}

a:link,
a:visited {
  text-decoration: none;
  color: inherit;
  font-family: inherit;
  display: inline-block;
  transition: color 0.15s;
}

a:hover,
a:active {
  text-decoration: none;
  color: inherit;
  font-family: inherit;
  display: inline-block;
}

.heading-primary {
  font-size: 3.6rem;
  font-weight: 700;
}

@media (max-width: 576px) {
  .heading-primary {
    font-size: 2.4rem;
    font-weight: 700;
  }
}

.btn-common,
.btn-common:link,
.btn-common:visited {
  border: none;
  transition: background-color 0.15s ease, box-shadow 0.15s ease;
  font-weight: 600;
  border-radius: 3px;
}

.btn-color-primary,
.btn-color-primary:link,
.btn-color-primary:visited {
  background-color: var(--color-btn-bg-primary);
  color: var(--color-btn-primary) !important;
}
.btn-color-primary:hover,
.btn-color-primary:hover,
.btn-color-primary:active {
  background-color: var(--color-btn-bg-primary-hover);
  box-shadow: 0px 5px 25px -1px rgba(0, 0, 0, 0.1);
}
.btn-color-secondary,
.btn-color-secondary:link,
.btn-color-secondary:visited {
  background-color: var(--color-btn-bg-secondary);
  color: var(--color-btn-secondary);
}
.btn-color-secondary:hover,
.btn-color-secondary:hover,
.btn-color-secondary:active {
  background-color: var(--color-btn-bg-secondary-hover);
  color: var(--color-btn-secondary-hover);
}

.paragraph-primary {
  font-size: 1.6rem;
  font-weight: 500;
}

.paragraph-secondary {
  font-size: 1.2rem;
}

.paragraph-secondary-small {
  font-size: 1rem;
}

.heading-secondary span {
  border-bottom: 0.5rem solid var(--color-theme);
}

/* SEARCH MODAL */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1021;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}
.custom-modal {
  z-index: 1022;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.hidden {
  display: none;
}
.custom-modal input {
  height: 60px;
  width: 100%;
  outline: none;
  border: none;
  font-family: inherit;
  color: inherit;
}
.custom-modal button[type="submit"] {
  height: 60px;
  width: 60px;
  position: absolute;
  bottom: 0;
  right: 0;
  border: none;
  color: #fff;
  background-color: #20dfe3;
  transition: color 0.15s, background-color 0.15s;
}

.custom-modal button[type="submit"]:hover {
  color: #20dfe3;
  background-color: #000;
}
/* SEARCH MODAL */

/* TOP NAVBAR */
.top-bar {
  background-color: var(--color-black);
  color: var(--color-white);
  letter-spacing: 0.05rem;
  font-size: 0.8rem;
}

.top-bar a:link,
.top-bar a:visited {
  color: var(--color-white);
  text-decoration: none;
}

.top-bar a:hover,
.top-bar a:active {
  color: var(--color-grey-light);
  text-decoration: none;
}

.top-bar i {
  color: var(--color-theme);
}
/* TOP NAVBAR */

/* MAIN NAVBAR */
.navbar {
  /* Four length values and a color */
  /* <length> | <length> | <length> | <length> | <color> */
  box-shadow: 0px 6px 25px -10px rgba(0, 0, 0, 0.1);
}

.navbar-toggler {
  border: none;
  outline: none;
}

.navbar-toggler:focus {
  border: none;
}

.navbar-toggler:focus,
.navbar-toggler:active,
.navbar-toggler-icon:focus {
  outline: none;
  box-shadow: none;
}

.navbar-toggler[aria-expanded="false"] .menu-close {
  display: none;
}

.navbar-toggler[aria-expanded="true"] .menu-open {
  display: none;
}

.navbar li li:hover,
.navbar li li:active {
  background-color: var(--color-theme);
}
/* MAIN NAVBAR */

/* HEADER SECTION  */
.section-hero {
  /* background-color: var(--color-bg-theme); */
  background: rgb(210, 249, 249);
  background: linear-gradient(
    0deg,
    rgba(210, 249, 249, 1) 0%,
    rgba(255, 255, 255, 1) 100%
  );
}
/* HEADER SECTION  */

/* SECTION CTA TOP */
.section-cta-top form .container {
  /* background-color: var(--color-bg-theme-dark); */
  background: rgb(85, 56, 244);
  background: linear-gradient(
    180deg,
    rgba(85, 56, 244, 1) 0%,
    rgba(34, 22, 98, 1) 100%
  );
  position: relative;
  top: -6rem;
  color: var(--color-white);
  /* Four length values and a color */
  /* <length> | <length> | <length> | <length> | <color> */
  box-shadow: 0px 5px 25px -1px rgba(0, 0, 0, 0.2);
}

.section-cta-top input,
.section-cta-top select {
  background-color: var(--color-form-field);
  color: var(--color-white);
  border: none;
  border-radius: 3px;
  /* Four length values and a color */
  /* <length> | <length> | <length> | <length> | <color> */
  box-shadow: 0px 5px 25px -1px rgba(0, 0, 0, 0.2);
}

.section-cta-top input::placeholder {
  color: var(--color-white);
  border-radius: 3px;
}

.section-cta-top *:focus {
  outline: none;
  box-shadow: 0 0 0 0.2rem var(--color-theme);
}

.anchor-cards {
  /* <length> | <length> | <length> | <length> | <color> */
  box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.1);
  position: relative;
  /* transform: scale(0.9); */
  transition: transform 0.15s;
}

.anchor-cards i:first-child {
  font-size: 4rem;
}

.anchor-cards i:last-child {
  font-size: 2rem;
  position: absolute;
  bottom: -2.4rem;
  background-color: var(--color-theme);
  width: 4rem;
  height: 4rem;
  color: var(--color-white);
  border-radius: 50%;
  opacity: 0;
  transform: scale(2);
  transition: opacity 0.15s, transform 0.15s, background-color 0.15s;
}

.anchor-cards i:last-child:hover {
  background-color: var(--color-black);
}

.anchor-cards-anchor:hover .anchor-cards i:last-child {
  opacity: 1;
  transform: scale(1);
}

.anchor-cards-anchor:hover .anchor-cards {
  background-color: var(--color-bg-theme-dark);
  color: var(--color-white);
  transform: scale(1.05);
}

.anchor-cards-anchor:hover .anchor-cards .text-secondary {
  color: var(--color-white) !important;
}
/* SECTION CTA TOP */

/* BEST COMPANY */

.best-company {
  background: rgb(210, 249, 249);
  background: linear-gradient(
    0deg,
    rgba(210, 249, 249, 1) 0%,
    rgba(255, 255, 255, 1) 100%
  );
}

.best-company .nav-link:link,
.best-company .nav-link:visited {
  border: 1px solid var(--color-grey);
  font-weight: 600;
  border-radius: 3px;
}

.best-company .nav-link:hover,
.best-company .nav-link:active {
  border: 1px solid var(--color-theme);
  color: var(--color-theme);
}

.best-company .nav-link.active {
  border: 1px solid var(--color-theme);
  color: var(--color-theme);
}

/* BEST COMPANY */

// SECTION PARTNERS
.section-partners img {
  // backdrop-filter: brightness(0);
  filter: brightness(0);
  opacity: 0.5;
  // height: 2rem;
}
// SECTION PARTNERS

/* SECTION SERVICES */
.section-services {
  background: rgb(210, 249, 249);
  background: linear-gradient(
    180deg,
    rgba(210, 249, 249, 1) 0%,
    rgba(255, 255, 255, 1) 100%
  );
}
/* SECTION SERVICES */

/* SECTION EXPERTS */
.section-experts {
  background: rgb(210, 249, 249);
  background: linear-gradient(
    0deg,
    rgba(210, 249, 249, 1) 0%,
    rgba(255, 255, 255, 1) 100%
  );
}

.profile {
  position: relative;
  max-width: 300px;
}

.profile-image {
  width: 100%;
  border-radius: 50%;
}

.profile-social-media-links {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  opacity: 0;
  transition: 0.15s ease;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  background-color: rgba(136, 116, 247, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--color-white);
}

.profile-container:hover .profile-social-media-links {
  opacity: 1;
}

.profile-social-media-links li a:link,
.profile-social-media-links li a:visited {
  font-size: 1.4rem;
}

.profile-social-media-links li a:hover,
.profile-social-media-links li a:active {
  color: var(--color-theme);
}
/* SECTION EXPERTS */

/* SECTION PROJECT */
.project {
  position: relative;
  overflow: hidden;
}

.project-image {
  display: block;
  transition: transform 0.15s ease;
}

.project-image-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.15s ease;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  background-color: rgba(136, 116, 247, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--color-white);
}

.project:hover .project-image-overlay {
  opacity: 1;
}

.project:hover .project-image {
  transform: scale(1.1);
}
/* SECTION PROJECT */

/* SECTION TESTIMONIALS */
.rating i {
  color: var(--color-gold);
}
/* SECTION TESTIMONIALS */

/* SECTION STATISTICS */
.section-statistics {
  background: rgb(85, 56, 244);
  background: linear-gradient(
    180deg,
    rgba(85, 56, 244, 1) 0%,
    rgba(34, 22, 98, 1) 100%
  );
}

.numbers {
  background: linear-gradient(to bottom, #fff, #20dfe3);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

