/* SECTION STATISTICS */

/* SECTION NEWS AND INSIGHTS */
.author-img {
  max-width: 50px;
  max-height: 50px;
}
/* SECTION NEWS AND INSIGHTS */

// SECTION CONTACT US
.section-contact-us a:link,
.section-contact-us a:visited {
  color: var(--color-theme);
}

.section-contact-us a:hover,
.section-contact-us a:active {
  color: inherit;
}
// SECTION CONTACT US

/* FOOTER */
.footer {
  background-color: var(--color-bg-dark);
  color: var(--color-white);
}

.footer a {
  transition: color 0.15s;
}

.footer a:hover,
.footer a:active {
  color: var(--color-theme);
}
/* FOOTER */
